  border: 1px solid var(--second);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: var(--few);
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 2px;
}

